html,
body,
#root,
.App {
  height: 100%;
  overflow: hidden;
}

.bg {
  height: 100%;
  width: 100%;
  object-fit: cover;
  opacity: 1;
}

.hidden {
  display: none;
}

.loading {
  height: 100%;
  width: 100%;
  background-color: #282c34;
  color: ghostwhite;
  display: flex;
  justify-content: center;
  align-items: center;
}
